import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL } from "../file.constants"
import { QuotaResponse } from "../file.types"

const getQuota = async () => {
  const { data } = await axiosClient.get<QuotaResponse>(`${FILES_BASE_URL}/quota`)
  return data
}

export { getQuota }
